* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; }
html {
    @include medium {
        font-size: 14px; }
    @include small {
        font-size: 12px; } }
body {
    display: flex;
    flex-flow: column; }

#app {
    &.loading {
        width: 100vw;
        height: 100vh;
        overflow-y: hidden;
        background-color: $dark;
        background-image: url('#{$assets}Notus-Logo-Loading.svg');
        background-position: center;
        background-repeat: no-repeat;
        .loading__text {
            color: $white;
            font-family: $font;
            font-size: $paragraph-size;
            text-align: center;
            z-index: 2;
            position: absolute;
            top: calc(50% + 210px);
            left: 50%;
            transform: translateX(-50%);
            margin: auto; } } }
