@mixin hover {
  &:not([disabled]):hover {
    @content; } }
@mixin font-bold {
    font-family: $font;
    font-weight: bold; }
@mixin large-only {
  	@media (min-width:$m) and (max-width: $l) {
     	@content; } }
@mixin large {
	@media (max-width: $l) {
		@content; } }

@mixin medium {
	@media (max-width: $m) {
		@content; } }

@mixin small {
	@media (max-width: $s) {
		@content; } }
@mixin Xsmall {
	@media (max-width: $xs) {
		@content; } }
